import Mock from 'mockjs'
import apiData from './data.json'

// 暴露商家接口: /shopinfo
Mock.mock('/shopinfo',{code:0,data:apiData.info});
// 暴露评价接口: /shopratings
Mock.mock('/shopratings',{code:0,data:apiData.ratings});
// 暴露商品接口: /shopgoods
Mock.mock('/shopgoods',{code:0,data:apiData.goods} );




